/* Table styling */
.table {
  width: 100%;
  margin-bottom: 1rem;
  color: #212529;
  border-collapse: collapse;
}

.table th,
.table td {
  padding: 0.75rem;
  vertical-align: middle;
  border-top: 1px solid #dee2e6;
}

.table thead th {
  vertical-align: bottom;
  border-bottom: 2px solid #dee2e6;
  background-color: #f8f9fa;
}

/* Hover effect */
.hover-row:hover {
  background-color: rgba(0, 0, 0, 0.03);
}

/* Button styling */
.btn-sm {
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  line-height: 1.5;
  border-radius: 0.5rem;
}

.btn-outline-primary {
  color: #007bff;
  background-color: #007bff;
  border-color: #007bff;
}

.btn-outline-primary:hover {
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
}

.btn-outline-danger {
  color: #dc3545;
  background-color: #dc3545;
  border-color: #dc3545;
}

.btn-outline-danger:hover {
  color: #fff;
  background-color: #dc3545;
  border-color: #dc3545;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .table-responsive {
    display: block;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
}


/* Insuarance Page */
.insurance-container {
  padding: 2rem 0;
}

/* Form Section Styles */
.insurance-item {
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #eee;
}

.file-info {
  background-color: #f8f9fa;
  padding: 0.75rem;
  border-radius: 0.25rem;
}

.remove-insurance-btn {
  transition: all 0.2s;
}

.remove-insurance-btn:hover {
  background-color: #dc3545;
  color: white !important;
}

.form-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;
}

/* Records Table Styles */
.insurance-records-table {
  width: 100%;
}

.insurance-records-table tbody tr {
  transition: background-color 0.2s;
}

.insurance-records-table tbody tr:hover {
  background-color: #f8f9fa;
}

.insurance-records-table td, 
.insurance-records-table th {
  vertical-align: middle;
  padding: 0.75rem;
}

.view-btn, .delete-btn {
  transition: all 0.2s;
}

.view-btn:hover {
  background-color: #0d6efd;
  color: white !important;
}

.delete-btn:hover {
  background-color: #dc3545;
  color: white !important;
}

/* Loading Spinner */
.loading-spinner {
  display: flex;
  justify-content: center;
  padding: 2rem 0;
}

/* Responsive Adjustments */
@media (max-width: 992px) {
  .insurance-container .row > div {
    padding-left: 0;
    padding-right: 0;
  }
  
  .form-actions {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .form-actions button {
    width: 100%;
  }
}